.about-who-are{
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 100vh;

     .who-are-bg{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: block;
        z-index: -1;
        

    }

.who-are-bg-m{
    display: none;
}

    .who-are-container{
        position: relative;
        display: flex;
        width: 100%;
        height: 100%;
        min-height: 100vh;
        
        padding: 0 100px;
        align-items: center;
        justify-content: space-around;
        .who-are-text{
          
            h1{
                color: $VioletColor;
                text-align: center;
                font-size: 3vw;
                margin: 0;
            }
            h2{
                color: $OrangeColor;
                text-align: center;
                font-size: 2.1vw;
                margin: 0;
            }
            h3{
                margin: 0;
                color:$VioletColor;
                text-align: center;
                font-size: 2vw;
                font-weight: bold;
                
            }
        }
         .who-are-image{
         
         width: 20vw;
            img{width: 100%;}
        }
    }

}


@media screen and(max-width:1024px) {
    .about-who-are{

        .who-are-bg-m{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            
            z-index: -1;
            display: block;
    
        }
        .who-are-bg{
        display: none;  
    
        }








        .who-are-container{
            padding:  0 ;
            // flex-wrap: wrap;
            .who-are-text{
          
                h1{
                   
                    font-size: 30px;
                    margin: 0;
                }
                h2{
           font-size: 35px;
                }
                h3{
                    margin: 0;
                    color:$VioletColor;
                    text-align: center;
                    font-size: 19px;
                    font-weight: bold;
                    
                }
             
        }
        .who-are-image{
         display: none;
           }

    }
    
}
}