.services-idea{
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    .services-idea-bg{
        position: absolute;
        z-index: -1;
        width: 100%;
        height: 100%;

    }



}
.services-idea-container{
    width: 100%;
    height: 100%;
    min-height: 100vh;
    display: flex;
padding: 100px 0;

    justify-content: space-around;
    .services-idea-title{
        width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
        border-right: 4px dashed $VioletColor;
        padding: 0 100px;


        h1{
            color:$OrangeColor;
            font-size: 2.2vw;
        }
        .services-idea-title-line {
         width: 1%;
        }
    }
.services-idea-description{
    width: 50%;
    padding: 0 50px;
    h1{
        color: $VioletColor;
        font-size:1vw;
    }
}

}


@media screen and(max-width:1024px) {
    .services-idea{
      
  
    // background-color: red;
    
    
    }
    .services-idea-container{
       flex-wrap: wrap;
    padding: 0;
    
        justify-content: start;
        .services-idea-title{
          width: 100%;
     
            border-right: none;
            padding: 0 ;
    
    
            h1{
                color:$OrangeColor;
                font-size: 23px;
            }
          
        }
    .services-idea-description{
        width: 100%;
        padding: 0 3px;
        h1{
            color: $VioletColor;
            font-size:15px;
        }
    }
    
    }
    
}