.template-product{
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    .template-bg{
        position: absolute;
        z-index: -1;
        width: 100%;
        height: 100%;

    }
}
.template-container{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    min-height: 100vh;
    justify-content: space-around;
    .each-template{
        flex:0 0  21%;
        
   margin-top: 50px;
        height: 350px;
        background-color: #FFFFFF;
            
              border-radius: 10px;
              .image-temblate{
                  width: 100%;
  
                  height: 315px;
                  border-top-left-radius:10px;
                  border-top-right-radius:10px ; 
                  img{width: 100%;
                    border-top-left-radius:10px;
                    border-top-right-radius:10px ; 
                        // height: 100%;
                        object-fit: cover;
                        height: 100%;
                }
              }
              .template-title{
                  text-align: center;
                  color: $OrangeColor;
                  font-size: 20px;
                  font-weight: bold;
              }
    }
}

@media screen and(max-width:1024px) {

    .template-container{
     
        .each-template{
            flex:0 0 48%;
            
       margin-top: 50px;
            height: 350px;
            background-color: #FFFFFF;
                justify-content: space-between;
                  border-radius: 10px;
                  .image-temblate{
                      width: 100%;
      
                      height: 315px;
                      border-top-left-radius:10px;
                      border-top-right-radius:10px ; 
                      img{width: 100%;
                        border-top-left-radius:10px;
                        border-top-right-radius:10px ; 
                            // height: 100%;
                            object-fit: cover;
                            height: 100%;
                    }
                  }
                  .template-title{
                      text-align: center;
                      color: $OrangeColor;
                      font-size: 20px;
                      font-weight: bold;
                  }
        }
    }


    
}