.services-business{
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 100vh;

    .services-business-bg{
        position: absolute;
        z-index: -1;
        width: 100%;
        height: 100%;
        min-height: 90vh;
    }


}
.services-business-container{
    width: 100%;
    height: 100%;
    justify-content: center;
    min-height: 100vh;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-items: center;
padding: 50px 100px;
    .services-business-text{
        
        h1{
            color: $VioletColor;
            font-size: 3.5vw;
            margin: 0;
        }
        h2{
            margin: 0;
            color: $OrangeColor;
            font-size: 2.8vw;
            margin-left: 10vw;
        }
    }
    .services-business-image{
        width: 100%;
     
        img{
           
            width: 100%;}
    }

}
.services-devlopment{
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    text-align: center;
    background-image: url(./assets/images/ServicesImage/ServicesDevBg.png);
    img{
    
        width: 80%;
    }
}
@media screen and(max-width:1024px){
    .services-business-container{
     text-align: center;
       padding: 0;
 
        .services-business-text{            
            h1{
                font-size: 25px;
            }
            h2{
        margin: 0;
                font-size: 16px;
            }
        }
        .services-business-image{
            width: 100%;
         
            img{
               
                width: 100%;}
        }
    
    }
    .services-devlopment{
        position: relative;
        width: 100%;
        height: 100%;
        min-height: 100%;
        text-align: center;
        background-image: url(./assets/images/ServicesImage/ServicesDevBg.png);
        img{
        
            width: 100%;
        }
    }

    
}