.servcices-digital{
    position: relative;     
    width: 100%;    
    height: 100%;
    min-height: 100vh;
    .services-digital-bg{
        position: absolute;
        z-index: -1;
        width: 100%;
        height: 100%;
    }
}
.services-digital-container{
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 100vh;
 
    padding: 50px 100px;
    display: flex;

    justify-content: space-between;

.services-digital-container-left{
    width: 50%;
  
    border-right: 3px dashed $VioletColor;

    h1{
    
        color: $VioletColor;
        font-size:2vw;

  
    }
.services-digital-image{
    width: 50%;
    display: block;
}

h2{
    color: $VioletColor;
    font-size: 1.5vw;
   
}

}

.services-digital-container-right{
    width: 50%;
    padding: 0 20px;
    h2{
        color: $VioletColor;
        font-size: 1.5vw;       
   }
}
}

@media screen and(max-width:1024px) {

    .services-digital-container{

     flex-wrap: wrap;
        padding: 0px 0;
    
    
    .services-digital-container-left{
        width: 100%;
      
        border-right:none;
    
        h1{
        
            color: $VioletColor;
            font-size:20px;
    
      
        }
    .services-digital-image{
        width: 100%;
    }
    
    h2{
        color: $VioletColor;
        font-size:15px;;
       
    }
    
    }
    
    .services-digital-container-right{
        width: 100%;
        padding: 0 0px;
        h2{
            color: $VioletColor;
            font-size: 15px;       
       }
    }
    }





















    
}