.product-ourproduct{
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    .our-product-bg{
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: -1;
        display: block;
    }
    .our-product-bg-m{
        display: none;
    }
}
.product-ourproduct-container{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    padding: 0 100px;
    h1{
        color: $VioletColor;
        font-size: 3vw;
        margin: 0;
    }
    h2{margin: 0;
        padding-left:  13vw;
        color: $OrangeColor;
        font-size: 2vw;
    }

    h3{margin: 0;
        padding-left:  21vw;
        color: $VioletColor;
        font-size: 1.5vw;
    }




}

@media screen and(max-width:1024px) {

    .product-ourproduct{
       
        .our-product-bg-m{
            display: block;
            width: 100%;
            height: 100%;
            position: absolute;
            z-index: -1;
            display: block;
        }
        .our-product-bg{
            display: none;
        }
    }
    .product-ourproduct-container{
        padding: 0 ;
        h1{
         
            font-size:20px;

        }
        h2{
            font-size: 15px;
        }
    
        h3{
            font-size: 13px;
        }
    }
}